import React from "react"
import { Line } from "react-chartjs-2"

function LineChart({data}) {
    return (
        <div className="chart-container">
            <Line
                data={data}
                options={{
                    plugins: {
                        title: {
                            display: true,
                        },
                        legend: {
                            display: false
                        }
                    },
                    scales: {
                        y: {
                            suggestedMin: 0.0,
                            suggestedMax: 4.0,
                        }
                    }
                }}
            />
        </div>
    );
}

export default LineChart;